<script>
import { MapState } from 'vuex';
import ProgressMixin from '../mixins/ProgressMixin';

import SceneHeadline from '../SceneHeadline';

export default {
	name: 'SceneContentClover',
	mixins: [ProgressMixin],
	components: {
		SceneHeadline,
	},
	props: {
		options: Object,
		// contentId: {
		// 	type: String,
		// 	default: 'butterfly',
		// },
		className: String,
	},
	mounted(){
		// console.log('mounted - this.options.name: ', this.options.name);
		this.$nextTick(this.setupScene);
	},
	// data() {
	//   return {
	//   };
	// },
	methods: {
		setupScene(){
			var bg = document.querySelector('.bg--main'),
				content = this.$refs.content;

			// this.introTl
			// 	.set(content, {autoAlpha: 0});

			this.introTl
				.add('start')
				.fromTo(bg, 1,
					{
						autoAlpha: 0,
						ease: Linear.easeNone,
					},
					{
						autoAlpha: 1,
						ease: Linear.easeNone,
					}
				)
				.add('content-start')
				.fromTo(content, 2,
					{
						// y: '+=' + this.$store.state.windowHeight / 3 + 'px',
						autoAlpha: 0,
						ease: Linear.easeNone,
						delay: -1,
					},
					{
						// y: '+=' + this.$store.state.windowHeight / 3 + 'px',
						autoAlpha: 1,
						ease: Linear.easeNone,
						delay: -1,
					}
				)
				.add('content-outro')
				.fromTo(content, 2,
					{
						// y: '+=' + this.$store.state.windowHeight / 3 + 'px',
						autoAlpha: 1,
						ease: Linear.easeNone,
					},
					{
						// y: '+=' + this.$store.state.windowHeight / 3 + 'px',
						autoAlpha: 0,
						ease: Linear.easeNone,
					}
				)
				.add('content-end')
				.fromTo(bg, 1,
					{
						autoAlpha: 1,
						ease: Linear.easeNone,
					},
					{
						autoAlpha: 0,
						ease: Linear.easeNone,
					}
				)
				.add('end')
				.progress(1)
				.progress(0)
				// .pause()
				;
		},
	},
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import '../../assets/global';

.scene-content {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	min-height: 100vh;
	color: #fff;
	// background-color: pink;
	opacity: 1;

	.scene-headline { // h2
		position: relative;
		font-size: rem(60);
		top: 4vw;
		left: 50%;
		transform: translateX(-50%);
		text-align: center;
	}

	.content {
		opacity: 0;
	}
}
</style>



