<template>
	<div class="scene-content inactive-pointer" :class="className">
		<!--slot/-->
		<h2 class="scene-headline" ref="headline">Butterfly content</h2>
		<!-- <div v-if="options.contentId === 'butterfly-content'" :class="options.classes">
		</div> -->

		<ButterflyAnimation
			ref="butterfly1"
			id="butterfly1"
			position="butterfly--top-right"
			width=500
			height=500
			v-on:animationCompleted="butterflyAnimationCompleted"
			/>

		<div class="content" ref="content">
			<div class="butterfly-card">
				<div class="butterfly-card__image-wrapper">
					<img src="/static/images/butterfly-img1.jpg" alt="Butterfly image 1">
				</div>
				<div class="text">
					Her er noget tekst
				</div>
			</div>
			<div class="butterfly-card">
				<div class="butterfly-card__image-wrapper">
					<img src="/static/images/butterfly-img2.jpg" alt="Butterfly image 2">
				</div>
				<div class="text">
					Her er noget tekst
				</div>
			</div>
			<div class="butterfly-card">
				<div class="butterfly-card__image-wrapper">
					<img src="/static/images/butterfly-img1.jpg" alt="Butterfly image 1">
				</div>
				<div class="text">
					Her er noget tekst
				</div>
			</div>
			<div class="butterfly-card">
				<div class="butterfly-card__image-wrapper">
					<img src="/static/images/butterfly-img2.jpg" alt="Butterfly image 2">
				</div>
				<div class="text">
					Her er noget tekst
				</div>
			</div>
			<div class="butterfly-card">
				<div class="butterfly-card__image-wrapper">
					<img src="/static/images/butterfly-img1.jpg" alt="Butterfly image 1">
				</div>
				<div class="text">
					Her er noget tekst
				</div>
			</div>
			<div class="butterfly-card">
				<div class="butterfly-card__image-wrapper">
					<img src="/static/images/butterfly-img2.jpg" alt="Butterfly image 2">
				</div>
				<div class="text">
					Her er noget tekst
				</div>
			</div>
			<div class="butterfly-card">
				<div class="butterfly-card__image-wrapper">
					<img src="/static/images/butterfly-img1.jpg" alt="Butterfly image 1">
				</div>
				<div class="text">
					Her er noget tekst
				</div>
			</div>
			<div class="butterfly-card">
				<div class="butterfly-card__image-wrapper">
					<img src="/static/images/butterfly-img2.jpg" alt="Butterfly image 2">
				</div>
				<div class="text">
					Her er noget tekst
				</div>
			</div>
			<div class="butterfly-card">
				<div class="butterfly-card__image-wrapper">
					<img src="/static/images/butterfly-img1.jpg" alt="Butterfly image 1">
				</div>
				<div class="text">
					Her er noget tekst
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { MapState } from 'vuex';
import ProgressMixin from '../mixins/ProgressMixin';

import SceneHeadline from '../SceneHeadline';
import ButterflyAnimation from '../animations/Butterfly';

export default {
	name: 'SceneContentButterfly',
	mixins: [ProgressMixin],
	components: {
		SceneHeadline,
		ButterflyAnimation,
	},
	props: {
		options: Object,
		// contentId: {
		// 	type: String,
		// 	default: 'butterfly',
		// },
		className: String,
		content: Object,
	},
	mounted(){
		// console.log('mounted - this.options.name: ', this.options.name);
		this.contentWidth = this.$refs.content.clientWidth;
		this.windowWidth = window.innerWidth;
		// console.log('contentWidth: ', this.contentWidth);
		this.$nextTick(this.setupScene);
	},
	data(){
		return {
			// contentWidth,
			// windowWidth,
		};
	},
	
	methods: {
		butterflyAnimationCompleted(){
			console.log('butterfly animation complete');
		},
		// runPrepare(prepareProgress){
		// 	if(this.scenePrepareRan === false && prepareProgress == true){
		// 		// console.log('now preparing - scene: ', this.options.name);
		// 		this.scenePrepared = true;
		// 		this.scenePrepareRan = true;
		// 	}
		// },
		// runPrepareReverse(prepareProgress){
		// 	if(this.scenePrepareReverseRan === false && prepareProgress == true){
		// 		// console.log('now preparing - scene: ', this.options.name);
		// 		this.sceneReversePrepared = true;
		// 		this.scenePrepareReverseRan = true;
		// 	}
		// },
		setupScene(){
			var bg = document.querySelector('.bg--main'),
				headline = this.$refs.headline,
				butterfly1 = this.$refs.butterfly1,
				content = this.$refs.content;

			// this.introTl
			// 	.set(content, {autoAlpha: 0});

			// console.log('content scene: ', this.options.name);

			this.introTl
				.add('intro-start')
				.to(bg, 1, {
					autoAlpha: 1,
					ease: Linear.easeNone,
				})
				.add('content-start')
				.fromTo(content, 5,
					{
						// y: '+=' + this.$store.state.windowHeight / 3 + 'px',
						// y: '50%',
						x: this.windowWidth + 'px',
						autoAlpha: 1,
						delay: -1,
						ease: Linear.easeNone,
					},
					{
						// y: '+=' + this.$store.state.windowHeight / 3 + 'px',
						// y: '50%',

						// x: '-' + this.contentWidth - 1800 + 'px',
						x: '-' + this.contentWidth + 'px',
						autoAlpha: 1,
						delay: -1,
						ease: Linear.easeNone,
					}
				)
				.fromTo(headline, .5,
					{
						autoAlpha: 0,
						ease: Linear.easeNone,
					},
					{
						autoAlpha: 1,
						ease: Linear.easeNone,
					},
					'content-start-=0.4'
				)
				.add('outro')
				.to(bg, 1, {
					autoAlpha: 0,
					delay: -0.8,
					ease: Linear.easeNone,
				})
				.to(headline, .5,
					{
						autoAlpha: 0,
						delay: -1,
						ease: Linear.easeNone,
					}
				)
				.add('end')
				.progress(1)
				.progress(0)
				// .pause()
				;

			this.introTl.addCallback(()=>{
				this.$refs.butterfly1.play();
			}, "intro-start");
			this.introTl.addCallback(()=>{
				this.$refs.butterfly1.setReverse();
				this.$refs.butterfly1.play();
			// }, "outro");
			}, "outro+=0.5");
		},
	},
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import '../../assets/global';

.scene--butterfly-content {
	opacity: 1;
	// background-color: red;

	.scene-headline { // h2
		position: relative;
		font-size: rem(60);
		top: 4vw;
		left: 50%;
		transform: translateX(-50%);
		text-align: center;
	}
	
	.content {
		position: absolute;
		top: 0;
		left: 0;
		transform: translateX(-100%);
		display: flex;
		flex-wrap: no-wrap;
		align-items: center;

		height: 100%;

		z-index: 2;
	}

	#butterfly1 {
		z-index: 1;
		position: absolute;
		top: 5%;
		left: 5%;

		width: rem(200);
		height: rem(200);
	}

	.butterfly-card {
		min-width: rem(200);
		width: 20vw;
		margin: 0 rem(20);
		// position: relative;
		// transform: translateY(-20%);
		
		&__image-wrapper {
			width: 100%;
			overflow: hidden;
			img {
				will-change: transform;
				transition: transform .5s ease;
				display: block;
				max-width: 100%;
			}
		}
		
		.text {
			display: block;
			// width: 100%;
			background-color: #fff;
			color: #222;
			padding: rem(20);
		}

		&:hover {
			.butterfly-card__image-wrapper img {
				transform: scale(1.03);
			}
		}
	}
}
</style>



