<template>
	<div class="scene-hero inactive-pointer" :class="className">
		<!-- <div class="bg" ref="bg"></div> -->
		<SceneHeadline :headline="options.headline" :manchet="options.manchet" ref="headline"/>

		<!--
			HERO Not in use since introducing only one video tag at top level
		-->
		<!-- <Hero :type="options.heroType" :src="options.heroSrc" ref="video"/> -->
	</div>
</template>

<script>
import { MapState } from 'vuex';
import ProgressMixin from '../mixins/ProgressMixin';

import Hero from '../Hero';
import SceneHeadline from '../SceneHeadline';

export default {
	name: 'SceneHero',
	mixins: [ProgressMixin],
	components: {
		Hero,
		SceneHeadline,
	},
	props: {
		heroType: {
			type: String,
			default: 'video',
		},
		heroSrc: {
			type: String,
		},
		headline: {
			type: String,
		},
		className: String,
		options: Object,
	},
	data() {
		return {
			// windowHeight: window.innerHeight,
			tl: null,
		};
	},
	created(){
		//this.outroTl = null; // reset what comes from mixin to disable outro animation
		if(this.options.contentId === 'butterflyHero'){
			this.introTl = null;
			this.tl = new TimelineMax({ paused: true }); // headline intro animation (overwrite mixin default)
		} else {
			this.outroTl = null;
		}
	},
	mounted(){
		// console.log('mounted - this.options.name: ', this.options.name);
		this.$nextTick(this.setupScene);
	},

	methods: {
		playIntro(){ // called by app.vue if state.showIntro is false
			console.log('ran hero intro?');
			this.tl.tweenFromTo('start', 'end');
			this.runPrepare();
			// this.introTl.play();

		},
		setupScene(){
			this.setupAnimations();
		},
		runPrepare(prepareProgress){
			// if(this.scenePrepareRan === false && prepareProgress == true){
				// console.log('now preparing - scene: ', this.options.name);

				// console.log('hero prepare? - play video? - src: ', this.heroSrc);
				// console.log('this.$refs: ', this.$refs);
				var heroVideo = document.querySelector('.main-video video');
				// console.log('prepare video src: ', this.options.heroSrc);
				heroVideo.src = this.options.heroSrc;
				heroVideo.play();

				// this.scenePrepared = true;
				// this.scenePrepareRan = true;
				// this.sceneReversePrepared = false;
				// this.scenePrepareReverseRan = false;
			//}
		},
		// runPrepareReverse(prepareProgress){ // for later reference (roen)
		// },
		
		setupAnimations(){
			// console.log('setting up animations "' + this.options.name + '" - this.$refs.headline.$el: ', this.$refs.headline.$el);
			// console.log('setting up animations "' + this.options.name + '" - this.$refs.headline.$el: ');

			// var bg = this.$refs.bg;
			var bg = document.querySelector('.bg--intro');

			if(this.options.contentId === 'butterflyHero'){
				var introDuration = 1.5;
				this.tl
					.add('start')
					// ----- Linear easing
					.fromTo(this.$refs.headline.$el, introDuration, 
						{ 
							y: '+=' + ((this.$store.state.windowHeight / 100) * this.$store.state.headlineAnimationDistance) +'px',
							// y: '400px',
							autoAlpha: 0,
							// ease: Linear.easeNone,
							ease: Power2.easeOut,
						},
						{ 
							y: '-50%',
							// y: '400px',
							autoAlpha: 1,
							// ease: Linear.easeNone,
							ease: Power2.easeOut,
						})
					.fromTo(
						bg, 
						introDuration, 
						{ 
							// y: '-=' + ((this.windowHeight / 100) * this.headlineAnimationDistance) + 'px',
							autoAlpha: 1,
							delay: -introDuration,
							ease: Linear.easeNone,
						}, 
						{ 
							// y: '-=' + ((this.windowHeight / 100) * this.headlineAnimationDistance) + 'px',
							autoAlpha: 0,
							delay: -introDuration,
							ease: Linear.easeNone,
						}
					)
					.add('end')
					.progress(1) // caching animation
					.progress(0) // set stort pos back to 0
					// .pause()
					;
				
				this.outroTl
					.add('start')
					.fromTo(this.$refs.headline.$el, 1,
						{ 
							y: '-50%',
							// y: '-400px',
							autoAlpha: 1,
							// delay: .2,
							ease: Linear.easeNone,
						},
						{ 
							y: '-=' + ((this.$store.state.windowHeight / 100) * this.$store.state.headlineAnimationDistance) + 'px',
							// y: '-400px',
							autoAlpha: 0,
							// delay: .2,
							ease: Linear.easeNone,
						}
					)
					.add('end')
					.progress(1) // caching animation
					.progress(0) // set stort pos back to 0
					// .pause()
					;
			} else {
				this.introTl
					.add('start')
					// ----- Linear easing
					.fromTo(this.$refs.headline.$el, 1, 
						{ 
							y: '+=' + ((this.$store.state.windowHeight / 100) * this.$store.state.headlineAnimationDistance) + 'px',
							// y: '400px',
							autoAlpha: 0,
							ease: Linear.easeNone,
						},
						{ 
							y: '-50%',
							// y: '400px',
							autoAlpha: 1,
							ease: Linear.easeNone,
						})
					.fromTo(
						bg, 
						1, 
						{ 
							// y: '-=' + ((this.windowHeight / 100) * this.headlineAnimationDistance) + 'px',
							autoAlpha: 1,
							delay: -1,
							ease: Linear.easeNone,
						}, 
						{ 
							// y: '-=' + ((this.windowHeight / 100) * this.headlineAnimationDistance) + 'px',
							autoAlpha: 0,
							delay: -1,
							ease: Linear.easeNone,
						}
					)
					.add('50')
					.to(this.$refs.headline.$el, 1,
						{ 
							y: '-=' + ((this.$store.state.windowHeight / 100) * this.$store.state.headlineAnimationDistance) + 'px',
							// y: '-400px',
							autoAlpha: 0,
							// delay: .2,
							ease: Linear.easeNone,
						}
					)
					.add('end')
					.progress(1) // caching animation
					.progress(0) // set stort pos back to 0
					// .pause()
					;
			}
		},
	},
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import '../../assets/global';

.scene-hero {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	color: white;

	.scene-headline {
		opacity: 0;
	}
}

</style>



